footer {
    padding: 4rem 6rem;
    background-color: #222;
    color: #fff;
    margin-top: 4rem;
}
.top {
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
}
.icon {
    color: #fff;
    font-size: 2rem;
    margin-left: 1rem;
    cursor: pointer;
}

.icon:hover {
    color: #01959a;
}
.bottom {
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.bottom div {
    display: flex;
    flex-direction: column;
}
.bottom h4 {
    font-size: 1.3rem;
    padding: 1rem 0 .8rem 0;
}
.bottom a {
    text-decoration: none;
    color: #fafafa;
    padding-bottom: 0.4rem;
    font-size: 1.1rem;
}

