.hero {
    max-width: 100vw;
    height: 100vh;
    position: relative;
    object-fit: fill;
}

.hero img {
    width: 100%;
    height: 100%;
}

.hero .info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: white;
}

.hero .info h1 {
    font-size: 3rem;
    font-weight: 800;
}

.hero .info p {
    font-size: 1.3rem;
    padding: 0.5rem 0 2rem 0 ;
}

.hero .info button {
    background: white;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
    border: none;

}